@import "mediaQueries.scss";

@import "mediaQueries.scss";

// Stripping styles

html {
    font-size: 62.5%;
}

body, div, section, img, h1, h2, h3, h4, p, span, ul, li {
    margin: 0;
    padding: 0;
}

h1 {
    font-size: 2.8rem;
    text-transform: uppercase;
    letter-spacing: .02rem;
}

body {
    color: black;
}

li {
    list-style: none;
}

a {
    text-decoration: none;
    color: black;
}

footer {
    height: 2rem;
    background-color: black;
    color: white;
    text-align: center;
}

// button {
