button {
    position: relative;
    text-align: center;
    z-index: 1;
    font-size: 1rem;
    padding: 12px 30px;
    border-radius: 25px;
    border: 1px solid black;
    background: white;
    color: black;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    transition: color .5s ease-out;
        &:hover {
            background: grey;
            opacity: 0.2;
            color: #131314;
            // transition: 1.5s;
            }
            &:hover, &:focus {
                cursor: pointer;
                &::after {
            display: block;
        }
    }
}

.wrapper {
    width: 91%;
    margin: 0 auto;
    @include smallLaptop {
        width: 85%;
    }
    @include tablet {
        width: 90%;
    }
}

.flexContent {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}